r: pointer;\n  padding: var(--zylem-spacing-sm) var(--zylem-spacing-md);\n  font-size: var(--zylem-font-size);\n  transition: background-color 0.2s ease, color 0.2s ease;\n}\n.zylem-button:hover {\n  background: var(--zylem-color-primary);\n  color: var(--zylem-color-background-translucent);\n}\n.zylem-button:active {\n  background: var(--zylem-color-active);\n  color: var(--zylem-color-background-translucent);\n}\n.floating-panel {\n  background: var(--zylem-color-background-translucent);\n  border: var(--zylem-border) solid var(--zylem-color-primary);\n  border-radius: var(--zylem-radius);\n  box-shadow: 0 var(--zylem-spacing-sm) 32px rgba(0, 0, 0, 0.4);\n  backdrop-filter: blur(var(--zylem-spacing-sm));\n  pointer-events: auto;\n}\n.floating-panel-titlebar {\n  padding: var(--zylem-spacing-sm) var(--zylem-spacing-md);\n  background: var(--zylem-color-accent);\n  border: 1px solid var(--zylem-color-background);\n  border-radius: calc(var(--zylem-radius) - 1px) calc(var(--zylem-radius) - 1px) 0 0;\n  font-family: var(--zylem-font-family);\n  font-weight: 600;\n  transition: background-color 0.15s ease;\n}\n.floating-panel-titlebar:hover {\n  background: var(--zylem-color-accent-hover);\n}\n.floating-panel-titlebar:active {\n  background: var(--zylem-color-accent-active);\n  cursor: grabbing;\n}\n.floating-panel-title {\n  font-size: var(--zylem-font-size);\n  color: var(--zylem-color-background);\n  text-shadow: 0 1px 2px rgba(0, 0, 0, 0.3);\n}\n.floating-panel-button {\n  all: unset;\n  cursor: pointer;\n  padding: 2px 6px;\n  font-size: 10px;\n  line-height: 1;\n  color: var(--zylem-color-background);\n  border-radius: calc(var(--zylem-radius) / 3);\n  transition: background-color 0.15s ease;\n}\n.floating-panel-button:hover {\n  background: rgba(0, 0, 0, 0.2);\n}\n.floating-panel-button:active {\n  background: rgba(0, 0, 0, 0.3);\n}\n.floating-panel-content {\n  background: transparent;\n}\n.floating-panel-controls {\n  display: flex;\n  align-items: center;\n  gap: var(--zylem-spacing-xs);\n}\n\n/* src/editor/toolbar.css */\n.zylem-toolbar {\n  display: flex;\n  position: sticky;\n  top: 0;\n  gap: var(--zylem-spacing-md);\n  padding: var(--zylem-spacing-md);\n  z-index: 1;\n}\n.zylem-toolbar.separator {\n  border-bottom: var(--zylem-border) solid var(--zylem-color-primary);\n}\n.zylem-toolbar-btn {\n  display: flex;\n  align-items: center;\n  padding: var(--zylem-spacing-md);\n  cursor: pointer;\n}\n.zylem-toolbar-btn:hover svg {\n  stroke: var(--zylem-color-background-translucent);\n}\n.zylem-toolbar-btn.selected {\n  background: var(--zylem-color-active);\n  border-color: var(--zylem-color-active);\n}\n.zylem-toolbar-btn.selected svg {\n  stroke: var(--zylem-color-background-translucent);\n}\n.zylem-icon {\n  width: var(--zylem-size-icon);\n  height: var(--zylem-size-icon);\n  stroke: var(--zylem-color-primary);\n}\n.zylem-tooltip {\n  background: var(--zylem-color-background-translucent);\n  color: var(--zylem-color-primary);\n  padding: var(--zylem-spacing-sm) var(--zylem-spacing-md);\n  margin-top: var(--zylem-spacing-sm);\n  border-radius: var(--zylem-radius);\n  border: var(--zylem-border) solid var(--zylem-color-primary);\n  font-size: var(--zylem-font-size);\n  pointer-events: none;\n  z-index: 1000;\n}\n.zylem-tooltip[data-enter] {\n  animation: tooltip-enter 0.2s ease-out;\n}\n@keyframes tooltip-enter {\n  from {\n    opacity: 0;\n    transform: translateY(4px);\n  }\n  to {\n    opacity: 1;\n    transform: translateY(0);\n  }\n}\n\n/* src/editor/console.css */\n.zylem-console-container {\n  display: flex;\n  flex-direction: column;\n  box-sizing: border-box;\n  min-height: 0;\n  padding: var(--zylem-spacing-sm);\n  height: 100%;\n}\n.zylem-console-wrapper {\n  position: relative;\n  display: flex;\n  flex: 1;\n  min-height: 0;\n}\n.zylem-console {\n  width: 100%;\n  height: 100%;\n  flex: 1;\n  resize: none;\n  background: var(--zylem-color-console-background);\n  color: var(--zylem-color-primary);\n  font-si