e;\n}\n.accordion-trigger[data-expanded]::after {\n  transform: rotate(180deg);\n}\n.panel-content {\n  display: flex;\n  flex-direction: column;\n  gap: var(--zylem-spacing-md);\n  padding: var(--zylem-spacing-md);\n  color: var(--zylem-color-primary);\n}\n\n/* src/editor/menu.css */\n.zylem-menu {\n  display: flex;\n  flex-direction: column;\n  height: 100%;\n  width: 100%;\n  background: var(--zylem-color-background-translucent);\n  border-radius: var(--zylem-radius);\n  box-sizing: border-box;\n  overflow-y: auto;\n  overflow-x: hidden;\n  pointer-events: auto;\n  font-family: var(--zylem-font-family);\n}\n.zylem-menu::-webkit-scrollbar {\n  display: none;\n}\n.zylem-menu-header {\n  display: flex;\n  align-items: center;\n  justify-content: space-between;\n  gap: var(--zylem-spacing-sm);\n  border-bottom: var(--zylem-border) solid var(--zylem-color-primary);\n}\n.zylem-collapse-button {\n  flex-shrink: 0;\n  padding: var(--zylem-spacing-sm);\n  font-size: 12px;\n  line-height: 1;\n  margin-right: var(--zylem-spacing-sm);\n}\n.zylem-menu h3,\n.zylem-menu h4 {\n  margin: 0;\n}\n\n/* src/editor/entities.css */\n.entities-list {\n  display: flex;\n  flex-direction: column;\n  gap: var(--zylem-spacing-md);\n  overflow-y: auto;\n  overflow-x: hidden;\n}\nul {\n  all: unset;\n}\n.entity-item-wrapper {\n  all: unset;\n  cursor: pointer;\n}\n.entity-item-wrapper.hovered .entity-item {\n  background-color: var(--zylem-color-success-hover);\n  color: var(--zylem-color-primary);\n}\n.entity-item-wrapper:active .entity-item {\n  background-color: var(--zylem-color-active);\n  color: var(--zylem-color-background-translucent);\n}\n.entity-item {\n  display: grid;\n  gap: var(--zylem-spacing-sm);\n  grid-template-columns: 1fr 1fr 1fr;\n  border: var(--zylem-border) outset var(--zylem-color-success-hover);\n  padding: var(--zylem-spacing-md);\n  margin: var(--zylem-spacing-md) 0;\n  font-weight: 600;\n  transition: background-color 200ms cubic-bezier(0.075, 0.82, 0.165, 1);\n}\n.entity-item.hovered {\n  background-color: var(--zylem-color-active-hover);\n}\n.entity-info-item {\n  display: grid;\n  grid-template-columns: min-content max-content;\n  gap: var(--zylem-spacing-sm);\n}\n\n/* src/editor/detached-panel.css */\n.detached-panel {\n  min-width: 250px;\n  min-height: 150px;\n  position: fixed;\n  display: flex;\n  flex-direction: column;\n}\n.detached-panel-titlebar {\n  padding: var(--zylem-spacing-sm) var(--zylem-spacing-md);\n  cursor: grab;\n  display: flex;\n  align-items: center;\n  justify-content: space-between;\n  user-select: none;\n}\n.detached-panel-titlebar:active {\n  cursor: grabbing;\n}\n.detached-panel-content {\n  background: var(--zylem-color-background-translucent);\n  flex: 1;\n  overflow: auto;\n  display: flex;\n  flex-direction: column;\n}\n.accordion-item--dragging {\n  opacity: 0.5;\n}\n.accordion-drag-ghost {\n  display: flex;\n  align-items: center;\n  justify-content: space-between;\n  background: var(--zylem-color-background-translucent);\n  border: 2px dotted var(--zylem-color-accent);\n  border-radius: var(--zylem-radius);\n  color: var(--zylem-color-primary);\n  font-family: var(--zylem-font-family);\n  backdrop-filter: blur(var(--zylem-spacing-sm));\n  box-shadow: 0 8px 24px rgba(0, 0, 0, 0.5), 0 0 0 1px var(--zylem-color-accent);\n  position: fixed;\n  z-index: 9999;\n  pointer-events: none;\n}\n.accordion-drop-indicator {\n  height: 4px;\n  background: var(--zylem-color-accent);\n  border-radius: 2px;\n  margin: var(--zylem-spacing-sm) var(--zylem-spacing-md);\n  box-shadow: 0 0 8px var(--zylem-color-accent);\n  animation: drop-indicator-pulse 0.8s ease-in-out infinite alternate;\n}\n@keyframes drop-indicator-pulse {\n  from {\n    opacity: 0.6;\n  }\n  to {\n    opacity: 1;\n  }\n}\n\n/* src/styles.css */\n/*# sourceMappingURL=styles.css.map */";